.main {
    max-width: 100%;
    &--header {
      font-style: normal;
      font-weight: bolder;
      line-height: 1.2;
      font-family: "Syncopate", "sans-serif";
      text-transform: uppercase;
      @include fluid-font(2rem, 3rem);
      margin: 0 0 1.5rem 0; //old value: 0 0.2625rem 0 0
    }
    &--normal {
      @include fluid-font(1rem, 1rem);
      margin: 0.5rem 0 2.5rem 0;
      font-weight: normal;
    }
    &--text {
      @include fluid-font(1.1rem, 1.5rem);
      max-width: 45rem; //replaced 48.8125rem -> 45rem;
      margin: 1.5rem auto;
    }
    &--italic {
      font-style: italic;
      max-width: 45rem; //changed 48.8125rem -> 45rem
      margin: 1.5rem auto;
    }
    &--green {
      color: $green;
    }
    /* title and desc: all pages - font */
    &__page {
      &--title {
        @include fluid-font(1rem, 1.5rem);
        font-weight: normal;
      }
      &--desc {
        @include fluid-font(1.3rem, 3rem);
        font-family: "Syncopate", "sans-serif";
        text-transform: uppercase;
        margin: 5rem 0;
      }
    }
  
    &__intro {
      margin: 10vh auto 0 auto; //0 auto;
      max-width: 100%;
  
      & img {
        width: 100%; //Analysis
        max-width: 100%;
      }
  
      @media screen and (max-width: 1024px) {
        & img {
          width: 100%;
          display: flex;
          justify-content: center;
        }
      }
    }
  
    @media screen and (min-width: 1256px) {
      &__about {
        display: flex;
        margin: 5rem 0;
        flex-flow: row wrap;
        padding-right: 6rem;
        // height: 100vh;

        &--mobile {
            display: none;
        }
      }
      &__avatar {
        width: 100%;
        flex: 1;
        max-width: 802px;
        margin-right: 10.625rem;
      }
      &__me {
        width: 100%; /* definir largura // old: 21.5vw */
        max-width: 414px;
        box-sizing: border-box;
      }
    }
    @media screen and (min-width: 1256px) and (max-width: 1599px) {
      &__about {
        display: flex;
        flex-flow: column wrap;
        padding-right: 6rem;
        margin: 5rem 0;
        height: 100vh;
      }
      &__avatar {
        width: 50vw;
        flex: 1;
      }
      &__me {
        width: 40vw; /* 30vw old value */
        padding-left: 5vw;
      }
    }
    @media screen and (max-width:1330px) {
      &__intro {
        height: 100%;
        padding: 3rem 0 5.5rem 0;
      }
    }
    @media screen and (max-width: 1255px) {
      &__about {
        display: flex;
        flex-flow: column wrap;

        &--mobile {
            padding: 0 2rem 1.5rem 1.75rem;
            text-align: left;
            height: 100%;
            // max-width: 550px;
            margin: 0 auto;
        }
      }
      &__avatar {
        max-width: 100%;
        margin: 0 auto;
      }
      &__me {
        padding: 3rem 3rem 3rem 1.75rem;
        text-align: left;
        height: 100%;
        max-width: 550px;
        margin: 0 auto;

        &--desktop {
            display: none;
        }

        & > a {
            margin-right: auto;
            margin-left: auto;
        }
      }
    }
  
    &__avatar img {
      width: 100%;
      max-width: 802px;
    }
    /* Service */
    &__works {
      margin: 0 auto;
      display: flex;
      flex-flow: column wrap;
      text-align: center;
      justify-content: center;
      padding: 8rem 2.5rem 5rem 4rem;
  
      &--figure {
        margin: 5rem 0 15rem 0;
      }
  
      > figure > h1 {
        margin: 5rem 0 8rem 0;
      }
  
      &--grid {
        padding: 0 26.5vw;
        margin-bottom: 10rem;
      }
  
      &--page {
        width: 560px;
        margin: 10rem auto;
  
        & p {
          line-height: 1.5;
        }
      }
  
      &--risk {
        text-decoration: line-through;
      }
    }
  
    @media screen and (max-width: 560px) {
      &__works--page {
        width: auto;
        padding: 0 1.75rem;
        margin-bottom: 5rem;
      }
    }
    &__view--pic {
      width: 100%;
      // display: block;
    }
    &__service--title {
      font: normal normal bolder 1.5rem "Syncopate", "sans-serif";
      text-transform: uppercase;
    }
    @media screen and (max-width: 1255px) {
      &__works {
        margin: 0 auto;
        display: block;
        padding: 0 1.5rem 2rem 1.5rem; // changed 0 1.5rem 10rem 1.5rem -> 0 1.5rem 2rem 1.5rem
        text-align: center;

        &--figure {
            margin: 5rem 0 8rem 0;
          }
  
        &--grid {
          padding: 0 0 6rem 0;
          margin-bottom: 0;
        }
      }
      &__view--pic {
        width: 100%;
        justify-content: center;
      }
      &__view {
        margin-top: 0.25rem;
      }
    }
    &__view {
      margin-top: 3.75rem;
    }
  
    /* blog */
    &__blog {
      display: block;
      padding: 0 3.4vw;
    }
  }